import React , {Componetnt} from "react";
import {Mutatuon} from "react-apollo";
import Form from "./styles/Form";
import gql from "graphql-tag";
import formatMoney from "../lib/formatMoney";

class CreateItem extends Componetnt{
    render(){
        return(
            <Form>
                <fieldset>
                    <label htmlFor="title">
                    Title
                     <input 
                     type="text"
                      id="title"
                       name="title"
                       placeholder="title"
                       required
                     />  
                    </label>
                </fieldset>

            </Form>
        );
    }
}

export default CreateItem;